import { Component, OnInit, OnDestroy, Renderer2, ViewChild, ElementRef, AfterViewInit, Inject, } from '@angular/core';
import {TranslateService} from '@ngx-translate/core';
import {MatDialog, MatDialogRef, MAT_DIALOG_DATA, MatSnackBar} from '@angular/material';

export interface DialogData {
  name: string;
  email: string;
}

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.scss']
})
export class HomeComponent implements OnInit, OnDestroy, AfterViewInit {
  etllist: any[] = ['Data Capture from different source: Dropbox, Google Drive, Sftp','Support Multiple file types and Formats','100+ Predefeined functions', 'Feasibility to track the loaded data status' ,'Schedule and Automate the Data Capturing and Streaming process' ];
  recon: any[] =['Rule Based Reconcilation Process',
  'Multiple Rule Types build with Filter, Functions and Tolerance',
  'Record or Batch wise reconciliation and Unreconcilation',
  'Over the Hood visuality of the Reconcilation results.']; 
  accounting: any[] = ['Activity Based Accounting']; 
  reports: any[] = ['Reporting Engine'];
  dashboard: any[] = ['Widget Based Dashboard', ];
  winScrolList: Function;
  accountinglist: any [];
  @ViewChild('vdo') vdo : ElementRef;
  name: string;
  email: string;

  constructor(
    private renderer: Renderer2,
    private translateService: TranslateService,
    public dialog: MatDialog,
    private eRef: ElementRef
  
  ) { }

  ngOnInit() {
    document.querySelector('app-nav').classList.add('hm-trns-nav');
  }

  checkVisible(elm) {
    var rect = elm.getBoundingClientRect();
    var viewHeight = Math.max(document.documentElement.clientHeight, window.innerHeight);
    return !(rect.bottom < 0 || rect.top - viewHeight >= 0);
  }

  @ViewChild('etlList') etlList: ElementRef;
  @ViewChild('recList') recList: ElementRef;
  ngAfterViewInit() {
    this.winScrolList = this.renderer.listen(window, 'scroll', (scrlEvt) => {
      //Navbar Scroll
      let scrollTop = document.documentElement.scrollTop;
      //Adding fixed class to navbar on scroll
      if (scrollTop >= 100) {
        document.querySelector('app-nav').classList.remove('hm-trns-nav');
      } else {
        document.querySelector('app-nav').classList.add('hm-trns-nav');
      }

      let windowHeight:any = window.innerHeight, farScroll = scrlEvt.target.scrollingElement.scrollTop;
      let actualScroll = windowHeight + farScroll;
      let etlListPos = this.etlList.nativeElement.getBoundingClientRect().top + window.screenY;
      let recListPos = this.recList.nativeElement.getBoundingClientRect().top + window.screenY;
      if (!this.etlList.nativeElement.querySelector('mat-list').classList.contains('list-anim')) {
        if (actualScroll > (etlListPos + 100)) {
          this.etlList.nativeElement.querySelector('mat-list').classList.add('list-anim');
        }
      }
      if (!this.recList.nativeElement.querySelector('mat-list').classList.contains('list-anim')) {
        if (actualScroll > (recListPos + 100)) {
          this.recList.nativeElement.querySelector('mat-list').classList.add('list-anim');
        }
      }

      //List
      /* if (this.checkVisible(this.etlList.nativeElement)) {
        const keylist = this.etlList.nativeElement.querySelector('mat-list');
        keylist.classList.add('list-anim');
      } else if (this.checkVisible(this.recList.nativeElement)) {
        const keylist = this.recList.nativeElement.querySelector('mat-list');
        keylist.classList.add('list-anim');
      } */
    });
  }

  ngOnDestroy() {
    // console.log(this.winScrolList);
    this.winScrolList();
    document.querySelector('app-nav').classList.remove('hm-trns-nav');
  }
  openDialog(): void {
    this.email = null;
    const dialogRef = this.dialog.open(HomeDialogComponent, {
      width: '250px',
      data: {name: this.name, email: this.email}
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
      this.email = result;
      console.log(this.email);
    });
  }


}

@Component({
  selector: 'home-dialog-component',
  templateUrl: 'home-dialog-component.html',
})
export class HomeDialogComponent {
  request: boolean;
  constructor(
    public dialogRef: MatDialogRef<HomeDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public data: DialogData,
    public snackBar: MatSnackBar) {}

  onNoClick(): void {
    this.dialogRef.close();
  }
  openSnackBar(action) {
    let msg = action == "posted" ? 'Request Sent!!' : 'Request Canceled!!';
    this.snackBar.open(msg,'', {
      duration: 500,
    });
  }  

}


