import {NgModule} from '@angular/core';
import {RouterModule, Routes} from '@angular/router';

import {HomeComponent} from './home/home.component';
import {LobComponent} from './entities/lob/lob.component';
import {AppConfig} from './config/app.config';
import {Error404Component} from './core/error404/error-404.component';
import {PlatformComponent} from './entities/platform/platform.component'

const routes: Routes = [
  {path: '', redirectTo: '/', pathMatch: 'full'},
  {path: '', component: HomeComponent},
  {path: AppConfig.routes.lobs, component: LobComponent},
  {path: AppConfig.routes.platform, component: PlatformComponent},
  {path: AppConfig.routes.error404, component: Error404Component},

  // otherwise redirect to 404
  {path: '**', redirectTo: '/' + AppConfig.routes.error404}
];

@NgModule({
  imports: [
    RouterModule.forRoot(routes)
  ],
  exports: [
    RouterModule
  ]
})

export class AppRoutingModule {
}
